.hero {
	min-height: 100vh;
	position: relative;
	padding: 0 30px;
	display: grid;
	grid-template-rows: 1fr 1fr;
	justify-items: center;
	background: url("../images/hero-back2.png") no-repeat center;
	background-size:cover;
	gap:15px;
	z-index: 3;
	&__image {
		align-self: flex-end;
		@include tablet {
		justify-self: right;
		}
	}
	&__avatar {
		width: 220px;
		@include tablet {
			width: 300px;
		}
	}
	&__article {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		height: 100%;
		@include tablet {
			justify-content: flex-end;
		}
	}
	&__title {
		@include sectionTitle();
		color: $blue;

		}
	&__text {
		font-size: 1em;
	}
	@include tablet {
		display: grid;
		grid-template-rows: none;
		grid-template-columns: 1fr 1fr;
		align-content: center;
		padding: 0 50px;
	}
	@include desktop {
		padding: 0 80px;

	}
}
#home {
	position: absolute;
}
